import Login from './Login'
import Logout from './Logout'
import Register from './Register'
import { StateContext } from '../Contexts'
import React, {useContext} from 'react'
import {Button} from 'react-bootstrap'
import { useState } from 'react'
import CreateTodo from '../CreateTodo'
import {Link} from 'react-navi'


export default function UserBar() {

  const Logout = React.lazy(() => import('./Logout'))
  const {state} = useContext(StateContext)

  const [showLogin, setShowLogin] = useState(false)
  const [showRegister, setShowRegister] = useState(false)
  const [showCreate, setShowCreate] = useState(false)
  const {userInfo} = state
  const{username} = userInfo


  if (username) {
      return (
        <div className="justify-content-end">
            <Button variant="link" onClick={(e) => setShowCreate(true)}>
             Create New Todo
            </Button>
            <CreateTodo show={showCreate} handleClose={() => setShowCreate(false)} />
            <Link href={'/users'}> See All Users </Link>
            <Logout />
        </div>
      )
  } else {
      return (
            <div className="justify-content-end">
                <Button variant="link" onClick={(e) => setShowLogin(true)}>
                    Login
                </Button>
                <Login show={showLogin} handleClose={() => setShowLogin(false)} />
                <Button variant="link" onClick={(e) => setShowRegister(true)}>
                    Register
                </Button>
                <Register show={showRegister} handleClose={() => setShowRegister(false)} />
            </div>

      
      )
  }
}
