import React from 'react'
import { useContext } from 'react';
import { StateContext } from './Contexts';
import { useResource } from 'react-request-hook';
import { useEffect } from 'react';
export default function Todo ({ title, description, dateCreated, dateCompleted, _id, userId}) {
 
    const {state,dispatch} = useContext(StateContext)
    const{userInfo} = state

    const [deletedTodo, deleteTodo ] = useResource((_id) => ({
        url: '/todo',
        method: 'delete',
        headers: {"Authorization": `${userInfo.access_token}`},
        data: {_id}
    }));

    useEffect(() => {
        if (deletedTodo && deletedTodo.data && deletedTodo.isLoading === false) {
            console.log(_id) //correct
            dispatch({type: 'DELETE_TODO', id: _id})
        }
    }, [,deletedTodo])

    const [toggledTodo, toggleTodo ] = useResource((_id, dateCompleted) => ({
        url: '/todo',
        method: 'patch',
        headers: {"Authorization": `${userInfo.access_token}`},
        data:{_id, dateCompleted}
    }))

    useEffect(() => {
        if (toggledTodo && toggledTodo.data && toggledTodo.isLoading === false) {
            dispatch({type: 'TOGGLE_TODO', id: _id})
        }
    }, [,toggledTodo])

    return (
    <div>
        <h3>{title}</h3>
        <div><i>{description}</i></div>
        <div >Created on : {dateCreated? dateCreated: new Date().toLocaleDateString('en-us')}</div>
        <div>{'Date completed:'.concat(dateCompleted)}</div>
        <label>
          Complete:
          <input
            type="checkbox"
            checked = {dateCompleted?true:false}
            onChange={e => {toggleTodo(_id, dateCompleted)}}
            />
        </label>
        <br />
        <button onClick={(e) => {deleteTodo(_id)}}>Delete</button>

    </div>        
    )
}