import React, {useState} from 'react'
import { StateContext } from './Contexts';
import { useContext } from 'react';
import { useResource } from 'react-request-hook';
import { useEffect } from 'react';
import { Modal, Form, Button } from 'react-bootstrap';


export default function CreateTodo ({show, handleClose}) {
    const {state, dispatch} = useContext(StateContext)
    const {userInfo} = state


    const [ todo, createTodo ] = useResource(({ title, description, dateCreated, dateCompleted}) => ({
        url: '/todo',
        method: 'post',
        headers: {"Authorization": `${userInfo.access_token}`},
        data: { title, description, dateCreated, dateCompleted}
    }))


    function handleCreate () {
        const dateCreated = new Date().toLocaleDateString('en-us');
        createTodo({ title, description,dateCreated, dateCompleted:""})
    }

    useEffect(() => {
        if(todo && todo.data){
            dispatch({ type: 'CREATE_TODO', data : todo.data})
        }

    }, [todo.data])


    const[title, setTitle] = useState('');
    const[description, setDescription] = useState('')

    function handleTitle(e){setTitle(e.target.value)}
    function handleDescription(e){setDescription(e.target.value)}
    
    return (

<Modal show={show} onHide={handleClose}>
<Form onSubmit={e => { e.preventDefault(); handleCreate(); handleClose() }}>
<Modal.Header closeButton>
    <Modal.Title>Create New Todo</Modal.Title>
</Modal.Header>
<Modal.Body>
  <Form.Label htmlFor="create-title">Title:</Form.Label>
  <Form.Control type="text" value={title} onChange={handleTitle} name="create-title" id="create-title" required/>
  <Form.Label htmlFor="description">Description:</Form.Label>
  <textarea value={description} onChange={handleDescription} className = "form-control" rows="5"/>
  </Modal.Body>
<Modal.Footer>
    <Button variant="secondary" onClick={handleClose}>Cancel</Button>
    <Button variant="primary" disabled={title.length === 0} type="submit">Create</Button>
</Modal.Footer>
</Form>
</Modal>

            )
        }

