function todoReducer(state, action){
    switch(action.type){
      case 'CREATE_TODO':
        const newTodoItem = {
            title:action.data.title,
            description: action.data.description,
            dateCompleted: action.data.dateCompleted,
            dateCreated: action.data.dateCreated,
            _id: action.data.id,
            author: action.data.author
          }
        return [newTodoItem, ...state]
      case 'TOGGLE_TODO': 
        return state.map(todo=> todo._id===action.id?{...todo, dateCompleted : (todo.dateCompleted ? '' : new Date().toLocaleDateString('en-us'))} : todo)
      case 'DELETE_TODO':
        return state.filter(todo => todo._id !== action.id)
      case 'FETCH_TODOS':
        return action.todoItems
      default:
        return state
    }
  }

  function userReducer (state, action) {
    switch (action.type) {
        case 'LOGIN':
        case 'REGISTER':
            return {
                'username': action.username,
                'access_token': action.access_token
            }
        case 'LOGOUT':
            return {
                'username': undefined,
                'access_token': undefined
            }
        default:
            return state;
    }
}


  export default function appReducer (state, action) {
    return {
        userInfo: userReducer(state.userInfo, action),
        todoItems: todoReducer(state.todoItems, action)
    }
}
